'use client';
import AdAuctionInsights from '@/components/Dashboard/AdAuctionInsights';
import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import AudienceInsights from '@/components/Dashboard/AudienceInsights';
import { 
  TrendingUp, 
  TrendingDown, 
  Eye, 
  MousePointer, 
  DollarSign, 
  Users, 
  Calendar,
  Filter,
  Download,
  RefreshCw,
  Search,
  Plus,
  MoreVertical,
  Play,
  Pause,
  Edit,
  Copy,
  Trash2,
  BarChart3,
  PieChart,
  Target,
  Globe,
  Smartphone,
  Monitor,
  AlertCircle,
  CheckCircle,
  Settings,
  Loader
} from 'lucide-react';

interface CampaignData {
  id: string;
  name: string;
  status: string | number;
  type?: string;
  budget?: number;
  budgetType?: string;
  spend?: number;
  impressions: number;
  clicks: number;
  ctr: number | string;
  avgCpc?: number;
  cpc?: string | number;
  cost: string | number;
  conversions?: number;
  conversionRate?: number;
  costPerConversion?: number;
  searchImpressionShare?: number;
  dates?: string[];
  platform?: string;
  currency?: string;
  dateRange?: string;
}

interface MetricCard {
  title: string;
  value: string;
  change: string;
  trend: 'up' | 'down';
  icon: React.ReactNode;
  color: string;
}

interface ApiResponse {
  success: boolean;
  data?: any[];
  summary?: {
    totalCampaigns?: number;
    totalCost?: string | number;
    totalClicks?: number;
    totalImpressions?: number;
    avgCtr?: number | string;
    avgCpc?: number | string;
    dateRange?: string;
    currency?: string;
  };
  error?: string;
  message?: string;
}

interface CurrencyOption {
  code: string;
  name: string;
  symbol: string;
  direction: 'ltr' | 'rtl';
}

interface ExchangeRates {
  [key: string]: number;
}

const GoogleAdsDashboard = () => {
  const [selectedPeriod, setSelectedPeriod] = useState('Last 7 days');
  const [selectedCampaigns, setSelectedCampaigns] = useState<string[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('All');
  const [isLoading, setIsLoading] = useState(true);
  const [campaigns, setCampaigns] = useState<CampaignData[]>([]);
  const [metricCards, setMetricCards] = useState<MetricCard[]>([]);
  const [apiError, setApiError] = useState<string | null>(null);
  const [apiSuccess, setApiSuccess] = useState<boolean>(false);
  const [accountCurrency, setAccountCurrency] = useState<string>('EGP'); // Default to EGP as you mentioned account is in EGP
  const [showCurrencySelector, setShowCurrencySelector] = useState<boolean>(false);
  const [selectedCurrency, setSelectedCurrency] = useState<string>('EGP'); // Default to EGP
  const [originalData, setOriginalData] = useState<{
    campaigns: CampaignData[],
    summary: {
      totalCost: number,
      totalClicks: number,
      totalImpressions: number,
      avgCtr: number,
      avgCpc: number
    }
  } | null>(null);
  const [exchangeRates, setExchangeRates] = useState<ExchangeRates>({});
  const [isLoadingRates, setIsLoadingRates] = useState<boolean>(true);
  const [ratesError, setRatesError] = useState<string | null>(null);
  const [lastRatesUpdate, setLastRatesUpdate] = useState<string>('');

  // Available currencies - All in English with fixed symbols
  const currencies: CurrencyOption[] = [
    { code: 'USD', name: 'US Dollar', symbol: '$', direction: 'ltr' },
    { code: 'EGP', name: 'Egyptian Pound', symbol: 'EGP', direction: 'ltr' },
    { code: 'SAR', name: 'Saudi Riyal', symbol: 'SAR', direction: 'ltr' },
    { code: 'AED', name: 'UAE Dirham', symbol: 'AED', direction: 'ltr' },
    { code: 'TRY', name: 'Turkish Lira', symbol: 'โบ', direction: 'ltr' },
    { code: 'EUR', name: 'Euro', symbol: 'โฌ', direction: 'ltr' },
    { code: 'GBP', name: 'British Pound', symbol: 'ยฃ', direction: 'ltr' },
    { code: 'KWD', name: 'Kuwaiti Dinar', symbol: 'KWD', direction: 'ltr' },
    { code: 'QAR', name: 'Qatari Riyal', symbol: 'QAR', direction: 'ltr' },
    { code: 'BHD', name: 'Bahraini Dinar', symbol: 'BHD', direction: 'ltr' },
    { code: 'OMR', name: 'Omani Rial', symbol: 'OMR', direction: 'ltr' },
    { code: 'JOD', name: 'Jordanian Dinar', symbol: 'JOD', direction: 'ltr' },
    { code: 'LBP', name: 'Lebanese Pound', symbol: 'LBP', direction: 'ltr' },
    { code: 'IQD', name: 'Iraqi Dinar', symbol: 'IQD', direction: 'ltr' },
  ];

  // Fetch exchange rates from API
  const fetchExchangeRates = async () => {
    try {
      setIsLoadingRates(true);
      setRatesError(null);
      
      // Using ExchangeRate-API (free tier)
      const response = await fetch('https://open.er-api.com/v6/latest/USD');
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      if (data && data.rates) {
        setExchangeRates(data.rates);
        setLastRatesUpdate(new Date(data.time_last_update_utc).toLocaleString());
        console.log('โ Exchange rates updated:', data.rates);
      } else {
        throw new Error('Invalid response format');
      }
    } catch (err: any) {
      console.error("Error fetching exchange rates:", err);
      setRatesError(`Failed to fetch exchange rates: ${err.message}`);
      
      // Fallback to hardcoded rates if API fails
      setExchangeRates({
        USD: 1,
        EGP: 50.61, // Based on the Google search result
        EUR: 0.92,
        GBP: 0.78,
        SAR: 3.75,
        AED: 3.67,
        TRY: 32.5,
        KWD: 0.31,
        QAR: 3.64,
        BHD: 0.38,
        OMR: 0.39,
        JOD: 0.71,
        LBP: 15000,
        IQD: 1310
      });
      setLastRatesUpdate('Using fallback rates');
    } finally {
      setIsLoadingRates(false);
    }
  };

  // Function to convert amount between currencies using live exchange rates
  const convertCurrency = (amount: number | string, fromCurrency: string, toCurrency: string): number => {
    const numericAmount = typeof amount === 'string' ? parseFloat(amount) : amount;
    
    // If currencies are the same, return the original amount
    if (fromCurrency === toCurrency) {
      return numericAmount;
    }
    
    // If we don't have exchange rates yet, return the original amount
    if (!exchangeRates || Object.keys(exchangeRates).length === 0) {
      return numericAmount;
    }
    
    // Get exchange rates
    const fromRate = exchangeRates[fromCurrency] || 1;
    const toRate = exchangeRates[toCurrency] || 1;
    
    // Convert to USD first (as base currency), then to target currency
    const amountInUSD = numericAmount / fromRate;
    return amountInUSD * toRate;
  };

  // Function to get currency symbol based on currency code
  const getCurrencySymbol = (currencyCode: string): string => {
    const currency = currencies.find(c => c.code === currencyCode);
    return currency ? currency.symbol : currencyCode;
  };

  // Function to get currency direction
  const getCurrencyDirection = (currencyCode: string): 'ltr' | 'rtl' => {
    const currency = currencies.find(c => c.code === currencyCode);
    return currency ? currency.direction : 'ltr';
  };

  // Function to format currency based on locale and currency code
  const formatCurrency = (amount: number | string, currencyCode: string): string => {
    const numericAmount = typeof amount === 'string' ? parseFloat(amount) : amount;
    
    // Format number with thousands separators
    const formattedNumber = numericAmount.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');
    
    // Add currency symbol in the appropriate position (always before for English display)
    const symbol = getCurrencySymbol(currencyCode);
    return `${symbol} ${formattedNumber}`;
  };

  // Function to get date range based on selected option
  const getDateRange = (option: string) => {
    const today = new Date();
    const yesterday = new Date(today);
    yesterday.setDate(today.getDate() - 1);

    const formatDate = (date: Date) => {
      const year = date.getFullYear();
      const month = (date.getMonth() + 1).toString().padStart(2, '0');
      const day = date.getDate().toString().padStart(2, '0');
      return `${year}-${month}-${day}`;
    };

    switch (option) {
      case "Today":
        return { startDate: formatDate(today), endDate: formatDate(today) };
      case "Yesterday":
        return { startDate: formatDate(yesterday), endDate: formatDate(yesterday) };
      case "Last 7 days":
        const last7Days = new Date(today);
        last7Days.setDate(today.getDate() - 6);
        return { startDate: formatDate(last7Days), endDate: formatDate(today) };
      case "Last 14 days":
        const last14Days = new Date(today);
        last14Days.setDate(today.getDate() - 13);
        return { startDate: formatDate(last14Days), endDate: formatDate(today) };
      case "Last 30 days":
        const last30Days = new Date(today);
        last30Days.setDate(today.getDate() - 29);
        return { startDate: formatDate(last30Days), endDate: formatDate(today) };
      case "Last 90 days":
        const last90Days = new Date(today);
        last90Days.setDate(today.getDate() - 89);
        return { startDate: formatDate(last90Days), endDate: formatDate(today) };
      default:
        return { startDate: formatDate(today), endDate: formatDate(today) };
    }
  };

  // Calculate CTR from clicks and impressions
  const calculateCTR = (clicks: number, impressions: number): number => {
    if (impressions === 0) return 0;
    return (clicks / impressions) * 100;
  };

  // Fetch campaigns data from Google Ads API via Next.js API Route
  const fetchCampaignsData = async (timePeriod: string) => {
    try {
      setIsLoading(true);
      setApiError(null);
      setApiSuccess(false);
      
      const customerId = "3271710441"; // Customer ID (without dashes)
      const { startDate, endDate } = getDateRange(timePeriod);
      
      console.log('๐ Calling Next.js API Route...');
      console.log('๐ Date range:', { startDate, endDate, customerId });
      
      // Call Next.js API Route instead of Supabase Edge Function
      const response = await fetch('/api/google-ads', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ 
          loginCustomerId: customerId,
          startDate: startDate,
          endDate: endDate,
          dataType: 'campaigns'
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const result = await response.json();
      
      console.log('โ API Response:', result);
      
      if (result && result.success) {
        // Process real Google Ads data
        let campaignsData: CampaignData[] = [];
        let summaryData = {
          totalCampaigns: 0,
          totalCost: 0,
          totalClicks: 0,
          totalImpressions: 0,
          avgCtr: 0,
          avgCpc: 0,
          dateRange: `${startDate} to ${endDate}`,
          currency: 'EGP' // Default to EGP as you mentioned account is in EGP
        };
        
        // Extract currency from data - IMPORTANT: Override API currency if it's wrong
        // Since you mentioned the account is in EGP but API shows USD
        let apiCurrency = 'EGP'; // Force EGP as the account currency
        
        setAccountCurrency(apiCurrency);
        
        // Check if we have data in the expected format
        if (result.data && Array.isArray(result.data) && result.data.length > 0) {
          campaignsData = result.data.map((item: any) => ({
            id: item.id || '',
            name: item.name || 'Unnamed Campaign',
            status: item.status || 'Unknown',
            type: item.type || 'Campaign',
            cost: item.cost || 0,
            clicks: item.clicks || 0,
            impressions: item.impressions || 0,
            ctr: item.ctr || calculateCTR(item.clicks || 0, item.impressions || 0),
            cpc: item.cpc || '0',
            dateRange: item.dateRange || '',
            dates: item.dates || [],
            platform: item.platform || 'google_ads',
            currency: apiCurrency // Force EGP as the currency
          }));
        }
        
        // Check if we have summary data
        if (result.summary) {
          summaryData = {
            totalCampaigns: result.summary.totalCampaigns || campaignsData.length,
            totalCost: parseFloat(String(result.summary.totalCost || 0)),
            totalClicks: result.summary.totalClicks || 0,
            totalImpressions: result.summary.totalImpressions || 0,
            avgCtr: parseFloat(String(result.summary.avgCtr || 0)),
            avgCpc: parseFloat(String(result.summary.avgCpc || 0)),
            dateRange: result.summary.dateRange || `${startDate} to ${endDate}`,
            currency: apiCurrency // Force EGP as the currency
          };
        } else {
          // Calculate summary from campaigns if no summary provided
          summaryData = {
            totalCampaigns: campaignsData.length,
            totalCost: campaignsData.reduce((sum, campaign) => sum + parseFloat(String(campaign.cost || 0)), 0),
            totalClicks: campaignsData.reduce((sum, campaign) => sum + (campaign.clicks || 0), 0),
            totalImpressions: campaignsData.reduce((sum, campaign) => sum + (campaign.impressions || 0), 0),
            avgCtr: 0,
            avgCpc: 0,
            dateRange: `${startDate} to ${endDate}`,
            currency: apiCurrency
          };
          
          // Calculate averages
          if (summaryData.totalImpressions > 0) {
            summaryData.avgCtr = (summaryData.totalClicks / summaryData.totalImpressions) * 100;
          }
          
          if (summaryData.totalClicks > 0) {
            summaryData.avgCpc = summaryData.totalCost / summaryData.totalClicks;
          }
        }
        
        // Store original data for currency conversion
        setOriginalData({
          campaigns: campaignsData,
          summary: {
            totalCost: summaryData.totalCost,
            totalClicks: summaryData.totalClicks,
            totalImpressions: summaryData.totalImpressions,
            avgCtr: summaryData.avgCtr || calculateCTR(summaryData.totalClicks, summaryData.totalImpressions),
            avgCpc: summaryData.avgCpc
          }
        });
        
        // Convert data to selected currency if different from account currency
        const convertedCampaigns = campaignsData.map(campaign => ({
          ...campaign,
          cost: selectedCurrency === apiCurrency ? campaign.cost : convertCurrency(campaign.cost, apiCurrency, selectedCurrency),
          cpc: campaign.cpc ? (selectedCurrency === apiCurrency ? campaign.cpc : convertCurrency(campaign.cpc, apiCurrency, selectedCurrency)) : undefined,
          avgCpc: campaign.avgCpc ? (selectedCurrency === apiCurrency ? campaign.avgCpc : convertCurrency(campaign.avgCpc, apiCurrency, selectedCurrency)) : undefined,
          currency: selectedCurrency
        }));
        
        const convertedSummary = {
          ...summaryData,
          totalCost: selectedCurrency === apiCurrency ? summaryData.totalCost : convertCurrency(summaryData.totalCost, apiCurrency, selectedCurrency),
          avgCpc: selectedCurrency === apiCurrency ? summaryData.avgCpc : convertCurrency(summaryData.avgCpc, apiCurrency, selectedCurrency),
          currency: selectedCurrency
        };
        
        setCampaigns(convertedCampaigns);
        setApiSuccess(true);
        
        // Calculate CTR for metrics card
        const calculatedCTR = summaryData.totalImpressions > 0 
          ? (summaryData.totalClicks / summaryData.totalImpressions) * 100 
          : 0;
        
        // Create metric cards from summary data
        const calculatedMetrics: MetricCard[] = [
          {
            title: 'Total Cost',
            value: formatCurrency(convertedSummary.totalCost, selectedCurrency),
            change: '+12.5%',
            trend: 'up',
            icon: <DollarSign className="w-6 h-6" />,
            color: 'bg-blue-500'
          },
          {
            title: 'Impressions',
            value: convertedSummary.totalImpressions.toLocaleString(),
            change: '+8.3%',
            trend: 'up',
            icon: <Eye className="w-6 h-6" />,
            color: 'bg-green-500'
          },
          {
            title: 'Clicks',
            value: convertedSummary.totalClicks.toLocaleString(),
            change: '+15.2%',
            trend: 'up',
            icon: <MousePointer className="w-6 h-6" />,
            color: 'bg-purple-500'
          },
          {
            title: 'CTR',
            value: `${calculatedCTR.toFixed(2)}%`,
            change: '+0.3%',
            trend: 'up',
            icon: <Target className="w-6 h-6" />,
            color: 'bg-orange-500'
          }
        ];
        
        setMetricCards(calculatedMetrics);
        setApiError(null);
      } else {
        // Handle API error
        const errorMessage = result.error || result.message || 'Unknown error occurred';
        setApiError(errorMessage);
        
        // Show demo data on error
        showDemoData();
      }
    } catch (err: any) {
      console.error("Error fetching campaigns data:", err);
      setApiError(`Failed to fetch data: ${err.message || 'Connection error occurred'}`);
      
      // Show demo data on error
      showDemoData();
    } finally {
      setIsLoading(false);
    }
  };

  // Show demo data when API fails
  const showDemoData = () => {
    const demoCurrency = 'EGP';
    setAccountCurrency(demoCurrency);
    
    const demoCampaigns = [
      {
        id: 'campaign-1',
        name: 'Search Campaign - Demo',
        status: 'Enabled',
        type: 'Search',
        budget: 4900,
        budgetType: 'Daily',
        spend: 1024,
        impressions: 1250,
        clicks: 89,
        ctr: 7.12,
        avgCpc: 11.51,
        cost: 1024,
        conversions: 12,
        conversionRate: 13.48,
        costPerConversion: 85.33,
        searchImpressionShare: 85.2,
        currency: demoCurrency
      },
      {
        id: 'campaign-2',
        name: 'Display Campaign - Demo',
        status: 'Enabled',
        type: 'Display',
        budget: 3000,
        budgetType: 'Daily',
        spend: 750,
        impressions: 3420,
        clicks: 156,
        ctr: 4.56,
        avgCpc: 4.81,
        cost: 750,
        conversions: 8,
        conversionRate: 5.13,
        costPerConversion: 93.75,
        searchImpressionShare: 72.8,
        currency: demoCurrency
      }
    ];
    
    // Store original demo data
    setOriginalData({
      campaigns: demoCampaigns,
      summary: {
        totalCost: 1774,
        totalClicks: 245,
        totalImpressions: 4670,
        avgCtr: 5.24,
        avgCpc: 7.24
      }
    });
    
    // Convert demo data to selected currency if different
    const convertedCampaigns = demoCampaigns.map(campaign => ({
      ...campaign,
      cost: selectedCurrency === demoCurrency ? campaign.cost : convertCurrency(campaign.cost, demoCurrency, selectedCurrency),
      cpc: campaign.cpc ? (selectedCurrency === demoCurrency ? campaign.cpc : convertCurrency(campaign.cpc, demoCurrency, selectedCurrency)) : undefined,
      avgCpc: campaign.avgCpc ? (selectedCurrency === demoCurrency ? campaign.avgCpc : convertCurrency(campaign.avgCpc, demoCurrency, selectedCurrency)) : undefined,
      currency: selectedCurrency
    }));
    
    setCampaigns(convertedCampaigns);
    
    const totalCost = selectedCurrency === demoCurrency ? 1774 : convertCurrency(1774, demoCurrency, selectedCurrency);
    const avgCpc = selectedCurrency === demoCurrency ? 7.24 : convertCurrency(7.24, demoCurrency, selectedCurrency);
    
    setMetricCards([
      {
        title: 'Total Cost',
        value: formatCurrency(totalCost, selectedCurrency),
        change: '+12.5%',
        trend: 'up' as const,
        icon: <DollarSign className="w-6 h-6" />,
        color: 'bg-blue-500'
      },
      {
        title: 'Impressions',
        value: '4,670',
        change: '+8.3%',
        trend: 'up' as const,
        icon: <Eye className="w-6 h-6" />,
        color: 'bg-green-500'
      },
      {
        title: 'Clicks',
        value: '245',
        change: '+15.2%',
        trend: 'up' as const,
        icon: <MousePointer className="w-6 h-6" />,
        color: 'bg-purple-500'
      },
      {
        title: 'CTR',
        value: '5.24%',
        change: '+0.3%',
        trend: 'up' as const,
        icon: <Target className="w-6 h-6" />,
        color: 'bg-orange-500'
      }
    ]);
  };

  // Update data when currency changes
  useEffect(() => {
    if (originalData && originalData.campaigns.length > 0) {
      const apiCurrency = accountCurrency;
      
      // Convert campaigns data
      const convertedCampaigns = originalData.campaigns.map(campaign => ({
        ...campaign,
        cost: selectedCurrency === apiCurrency ? campaign.cost : convertCurrency(campaign.cost, apiCurrency, selectedCurrency),
        cpc: campaign.cpc ? (selectedCurrency === apiCurrency ? campaign.cpc : convertCurrency(campaign.cpc, apiCurrency, selectedCurrency)) : undefined,
        avgCpc: campaign.avgCpc ? (selectedCurrency === apiCurrency ? campaign.avgCpc : convertCurrency(campaign.avgCpc, apiCurrency, selectedCurrency)) : undefined,
        currency: selectedCurrency
      }));
      
      setCampaigns(convertedCampaigns);
      
      // Convert summary data
      const convertedTotalCost = selectedCurrency === apiCurrency ? 
        originalData.summary.totalCost : 
        convertCurrency(originalData.summary.totalCost, apiCurrency, selectedCurrency);
      
      const convertedAvgCpc = selectedCurrency === apiCurrency ? 
        originalData.summary.avgCpc : 
        convertCurrency(originalData.summary.avgCpc, apiCurrency, selectedCurrency);
      
      // Update metric cards
      const updatedMetricCards = [
        {
          title: 'Total Cost',
          value: formatCurrency(convertedTotalCost, selectedCurrency),
          change: '+12.5%',
          trend: 'up' as const,
          icon: <DollarSign className="w-6 h-6" />,
          color: 'bg-blue-500'
        },
        {
          title: 'Impressions',
          value: originalData.summary.totalImpressions.toLocaleString(),
          change: '+8.3%',
          trend: 'up' as const,
          icon: <Eye className="w-6 h-6" />,
          color: 'bg-green-500'
        },
        {
          title: 'Clicks',
          value: originalData.summary.totalClicks.toLocaleString(),
          change: '+15.2%',
          trend: 'up' as const,
          icon: <MousePointer className="w-6 h-6" />,
          color: 'bg-purple-500'
        },
        {
          title: 'CTR',
          value: `${originalData.summary.avgCtr.toFixed(2)}%`,
          change: '+0.3%',
          trend: 'up' as const,
          icon: <Target className="w-6 h-6" />,
          color: 'bg-orange-500'
        }
      ];
      
      setMetricCards(updatedMetricCards);
    }
  }, [selectedCurrency, exchangeRates]);

  // Fetch exchange rates on component mount
  useEffect(() => {
    fetchExchangeRates();
  }, []);

  // Fetch campaigns data when period changes or exchange rates are loaded
  useEffect(() => {
    if (!isLoadingRates) {
      fetchCampaignsData(selectedPeriod);
    }
  }, [selectedPeriod, isLoadingRates]);

  // Refresh exchange rates every hour
  useEffect(() => {
    const interval = setInterval(() => {
      fetchExchangeRates();
    }, 3600000); // 1 hour in milliseconds
    
    return () => clearInterval(interval);
  }, []);

  const normalizeStatus = (status: string | number): string => {
    if (status === 'ENABLED' || status === 2) return 'Enabled';
    if (status === 'PAUSED' || status === 3) return 'Paused';
    if (status === 'REMOVED' || status === 4) return 'Removed';
    return String(status);
  };

  const getStatusColor = (status: string | number) => {
    const normalizedStatus = normalizeStatus(status);
    switch (normalizedStatus) {
      case 'Enabled':
        return 'text-green-600 bg-green-50';
      case 'Paused':
        return 'text-yellow-600 bg-yellow-50';
      case 'Removed':
        return 'text-red-600 bg-red-50';
      default:
        return 'text-gray-600 bg-gray-50';
    }
  };

  const getCampaignTypeIcon = (type: string = 'Search') => {
    switch (type) {
      case 'Search':
        return <Search className="w-4 h-4" />;
      case 'Display':
        return <Monitor className="w-4 h-4" />;
      case 'Shopping':
        return <DollarSign className="w-4 h-4" />;
      case 'Video':
        return <Play className="w-4 h-4" />;
      case 'Performance Max':
        return <Target className="w-4 h-4" />;
      default:
        return <Globe className="w-4 h-4" />;
    }
  };

  const filteredCampaigns = campaigns.filter(campaign => {
    const matchesSearch = campaign.name.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = filterStatus === 'All' || normalizeStatus(campaign.status) === filterStatus;
    return matchesSearch && matchesStatus;
  });

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center">
              <div className="w-8 h-8 rounded-full bg-gradient-to-r from-blue-500 to-green-500 flex items-center justify-center mr-3">
                <span className="text-white text-lg font-bold">G</span>
              </div>
              Google Ads
            </h1>
            <p className="text-gray-600 mt-1">Manage and monitor your advertising campaigns</p>
          </div>
          <div className="flex items-center space-x-3">
            <button
              onClick={() => fetchCampaignsData(selectedPeriod)}
              disabled={isLoading}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center disabled:opacity-50"
            >
              <RefreshCw className={`w-4 h-4 mr-2 ${isLoading ? 'animate-spin' : ''}`} />
              Refresh Data
            </button>
            <select 
              value={selectedPeriod}
              onChange={(e) => setSelectedPeriod(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option>Last 7 days</option>
              <option>Today</option>
              <option>Yesterday</option>
              <option>Last 14 days</option>
              <option>Last 30 days</option>
              <option>Last 90 days</option>
            </select>
            <button className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors flex items-center">
              <Download className="w-4 h-4 mr-2" />
              Export Data
            </button>
            <div className="relative">
              <button 
                onClick={() => setShowCurrencySelector(!showCurrencySelector)}
                className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors flex items-center"
              >
                <Settings className="w-4 h-4 mr-2" />
                {getCurrencySymbol(selectedCurrency)} {selectedCurrency}
              </button>
              
              {/* Currency Selector Dropdown */}
              {showCurrencySelector && (
                <div className="absolute right-0 mt-2 w-64 bg-white rounded-lg shadow-lg border border-gray-200 z-50">
                  <div className="p-3 border-b border-gray-200">
                    <h3 className="font-medium text-gray-700">Select Currency</h3>
                    <p className="text-xs text-gray-500 mt-1">
                      Choose the currency for display
                      {!isLoadingRates && !ratesError && (
                        <span className="block mt-1">Last updated: {lastRatesUpdate}</span>
                      )}
                    </p>
                  </div>
                  
                  {isLoadingRates && (
                    <div className="p-4 flex items-center justify-center">
                      <Loader className="w-4 h-4 animate-spin mr-2 text-blue-500" />
                      <span className="text-sm text-gray-600">Loading exchange rates...</span>
                    </div>
                  )}
                  
                  {ratesError && (
                    <div className="p-3 text-xs text-red-600">
                      {ratesError}. Using fallback rates.
                    </div>
                  )}
                  
                  <div className="max-h-64 overflow-y-auto">
                    {currencies.map((currency) => (
                      <button
                        key={currency.code}
                        onClick={() => {
                          setSelectedCurrency(currency.code);
                          setShowCurrencySelector(false);
                        }}
                        className={`w-full text-left px-4 py-2 hover:bg-gray-50 flex items-center justify-between ${
                          selectedCurrency === currency.code ? 'bg-blue-50 text-blue-600' : ''
                        }`}
                      >
                        <span>{currency.name}</span>
                        <span className="font-medium">{currency.symbol}</span>
                      </button>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* API Status Alert */}
        {apiError && (
          <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center">
            <AlertCircle className="w-5 h-5 text-red-500 mr-3" />
            <div>
              <p className="text-red-800 font-medium">API Connection Issue</p>
              <p className="text-red-600 text-sm">{apiError}</p>
              <p className="text-red-600 text-sm">Showing demo data instead.</p>
            </div>
          </div>
        )}

        {apiSuccess && (
          <div className="mb-4 p-4 bg-green-50 border border-green-200 rounded-lg flex items-center">
            <CheckCircle className="w-5 h-5 text-green-500 mr-3" />
            <div>
              <p className="text-green-800 font-medium">Google Ads API Connected Successfully</p>
              <p className="text-green-600 text-sm">
                Showing real data from your Google Ads account. 
                <span className="font-medium"> API Currency: {getCurrencySymbol(accountCurrency)} {accountCurrency}</span>
                {accountCurrency !== selectedCurrency && (
                  <span className="font-medium"> | Display Currency: {getCurrencySymbol(selectedCurrency)} {selectedCurrency}</span>
                )}
              </p>
            </div>
          </div>
        )}

        {/* Search and Filter Bar */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center space-x-4">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <input
                type="text"
                placeholder="Search campaigns..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent w-64"
              />
            </div>
            <select 
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value)}
              className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="All">All Status</option>
              <option value="Enabled">Enabled</option>
              <option value="Paused">Paused</option>
              <option value="Removed">Removed</option>
            </select>
          </div>
          <button className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center">
            <Plus className="w-4 h-4 mr-2" />
            Create Campaign
          </button>
        </div>
      </div>

      {/* Metrics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {metricCards.map((card, index) => (
          <div key={index} className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center justify-between mb-4">
              <div className={`w-12 h-12 rounded-lg ${card.color} flex items-center justify-center text-white`}>
                {card.icon}
              </div>
              <div className={`flex items-center text-sm ${card.trend === 'up' ? 'text-green-600' : 'text-red-600'}`}>
                {card.trend === 'up' ? <TrendingUp className="w-4 h-4 mr-1" /> : <TrendingDown className="w-4 h-4 mr-1" />}
                {card.change}
              </div>
            </div>
            <h3 className="text-gray-600 text-sm font-medium mb-1">{card.title}</h3>
            <p className="text-2xl font-bold text-gray-900">{card.value}</p>
          </div>
        ))}
      </div>

      {/* Campaigns Table */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200">
        <div className="px-6 py-4 border-b border-gray-200">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-gray-900">Campaigns</h2>
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-500">
                {filteredCampaigns.length} campaigns
              </span>
              <button className="p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <Filter className="w-4 h-4 text-gray-500" />
              </button>
            </div>
          </div>
        </div>

        {isLoading ? (
          <div className="p-8 text-center">
            <RefreshCw className="w-8 h-8 animate-spin mx-auto mb-4 text-blue-500" />
            <p className="text-gray-600">Loading campaigns data...</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Campaign
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Cost
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Impressions
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Clicks
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    CTR
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Avg. CPC
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredCampaigns.map((campaign) => (
                  <tr key={campaign.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 mr-3">
                          {getCampaignTypeIcon(campaign.type)}
                        </div>
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {campaign.name}
                          </div>
                          <div className="text-sm text-gray-500">
                            {campaign.type || 'Campaign'}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(campaign.status)}`}>
                        {normalizeStatus(campaign.status)}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {formatCurrency(campaign.cost, selectedCurrency)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {campaign.impressions.toLocaleString()}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {campaign.clicks.toLocaleString()}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {typeof campaign.ctr === 'number' 
                        ? campaign.ctr.toFixed(2) 
                        : parseFloat(String(campaign.ctr)).toFixed(2)}%
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {campaign.cpc 
                        ? formatCurrency(campaign.cpc, selectedCurrency)
                        : campaign.avgCpc 
                          ? formatCurrency(campaign.avgCpc, selectedCurrency)
                          : formatCurrency(0, selectedCurrency)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <div className="flex items-center space-x-2">
                        <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                          <Edit className="w-4 h-4" />
                        </button>
                        <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                          <Copy className="w-4 h-4" />
                        </button>
                        <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                          <MoreVertical className="w-4 h-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        {!isLoading && filteredCampaigns.length === 0 && (
          <div className="p-8 text-center">
            <Search className="w-12 h-12 mx-auto mb-4 text-gray-400" />
            <p className="text-gray-600 mb-2">No campaigns found</p>
            <p className="text-gray-500 text-sm">Try adjusting your search or filter criteria</p>
          </div>
        )}
      </div>

      {/* Additional Components */}
      <div className="mt-8 grid grid-cols-1 lg:grid-cols-2 gap-8">
        <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
          <h3 className="text-lg font-semibold mb-4">Ad Auction Insights</h3>
          <p className="text-gray-500">Data will be displayed here when available.</p>
        </div>
        <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
          <h3 className="text-lg font-semibold mb-4">Audience Insights</h3>
          <p className="text-gray-500">Data will be displayed here when available.</p>
        </div>
      </div>
    </div>
  );
};

export default GoogleAdsDashboard;